window.onload = function() {

  $(window).on('scroll', function() {
    if ($(this).scrollTop() > 50) {
      $('.navbar').addClass('affi');
    } else {
      $('.navbar').removeClass('affi'); 
    }   
  }); 

  $('#navbarNav a.page-navigation, .navbar-brand.page-navigation').on('click', function() {
    var target = $(this).attr("href");
    if (target == "home") {
      smoothScroll($(target), 0);
    } else if ($(window).width() < 768) {
      smoothScroll($(target), -30);
    } else {  
      smoothScroll($(target), -87);
    }
  });

  function smoothScroll(target, offset) {
    if (!offset) offset = 0;
    $('body,html').animate({ 'scrollTop': target.offset().top + offset },
      500
    );
  }  

  // btn collapse trigger
  $(function() {
    $('#navbarNav a').click(function() {
      if ($(window).width() < 768) {
        $('button.navbar-toggler').trigger('click');
      }
    });
  }); 





 
 

 

  var $form = $('form#test-form'),
      url = 'https://script.google.com/macros/s/orUMouGLI1v9qF8aFJLuqS9uS3Z3RtoqhH7A/exec'

  $('#submit-form').on('click', function(e) {
    e.preventDefault();
    var jqxhr = $.ajax({
      url: url,
      method: "GET",  
      dataType: "json",
      data: $form.serializeObject(),
      success: function () { 
        console.log(111);
      }
    })
  })
   
}    
  
 
 

